body {
  font-family: "Pontano Sans";
}

.page-headings-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  position: relative;
  margin-left: 10%;
  margin-right: 10%;
  margin-top: 20px;
  margin-bottom: 20px;
}

.page-headings {
  display: flex;
  width: 100vw;
  font-size: 15px;
  font-weight: bold;
  cursor: pointer;
}

.board {
  text-align: center;
}

.leader-board {
  margin: 40px;
  font-weight: bold;
  color: rgb(4, 52, 71);
}

.heading-backtest {
  margin: 12px;
  font-weight: bold;
  color: rgb(4, 52, 71);
  display: inline-block;
}

.header-container {
  margin: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.change-slippage {
  display: flex;
}

.selector {
  border: 1px solid grey;
  border-radius: 8px;
}

.headings {
  font-weight: bold;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.first {
  background-color: goldenrod;
}

.second {
  background-color: silver;
}

.third {
  background-color: saddlebrown;
}

.ranking {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}